#contactpage{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.clientsTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.clienttDesc{
    font-weight: 300;
    font-size: 1rem;
}
.clientImgs{
    margin: 1rem 0;
    display: flex;
    flex-wrap: wrap;
}
.clientImg{
    object-fit: cover;
    width: 10%;
    min-width: 9rem;
    margin:0 0.75rem;
    padding: 0.25rem;
}
#clients{
    padding: 2rem;
}
.contactDesc{
    font-size: 1rem;
    font-weight: 300;
    padding: 1rem;
    
}
.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
    

}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color:white;
    border-radius: 0.5rem;
    background: rgb(40,40,40);
    border: none;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
    padding-top: 0.5rem;
}
.submitbtn{
    background: white;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;
}
.footer{
    width: 100vw;
    height: 4rem;
    background: rgb(40,40,40);
    display: flex;
    align-items: center;
    font-size: small;
    justify-content: center;
}