#projects{
    min-height: calc(100vh-4rem);
    margin: 0 auto;
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
.workTitle{
    margin: 1rem 0;
    font-size: 3rem;
}
.workImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    max-width: 65rem;
}
.workDesc{
    font-size: 1.5rem;

}
.worksImg{
    object-fit: cover;
    height: 15rem;
    width: 15rem;
    margin: 1.5rem;
}
