.navbar{
    height: 5rem;
    width: 100vw;
    margin: 0 auto;
    padding: 1rem 3rem;
    display: flex;
    align-content: center;
    justify-content: center;
    position: sticky;
    top: 0;
    z-index: 3;
    /* max-width: 225rem; */
}
.desktopMenuList{
    margin: 1rem;
    cursor: pointer;
}
.desktopMenuList:hover{
    color: yellow;
    padding-bottom: 0.5rem;
}
.menuBtn{
    background: white;
    color: black;
    border: none;
    border-radius: 0.5rem;
    margin: 2rem;
    padding: 0.75rem 3.5rem;

}